.apply {
  padding-top: 32rem;
  padding-bottom: 16rem;
  background-color: #fcfcfc;
  margin-top: -17rem;
  // min-height: 700px;
  text-align: center;
  position: relative;
  z-index: 2;

  &__title {
    font-weight: 900;
    font-size: 48px;
    line-height: 1;
    margin-bottom: 5.6rem;
    position: relative;
    z-index: 2;
  }

  &__subtitle {
    font-weight: 600;
    font-size: 2.4rem;
    color: #334563;
    margin-bottom: 5.5rem;
    position: relative;
    z-index: 2;
  }

  &__button {
    position: relative;
    padding: 16px 88px;
    background-color: $c-btn-primary;
    color: #fff;
    z-index: 5;

    &:hover {
      background-color: darken($color: $c-btn-primary, $amount: 12);
    }
  }
}

.shapes {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;

  &__item {
    position: absolute;
    display: block;
    list-style: none;
    width: 56px;
    height: 143px;
    border: 5px solid $color-primary;
    border-radius: 64px;
    animation: animate-shapes 25s linear infinite;
    bottom: -150px;

    &--1 {
      left: 25%;
      width: 56px;
      height: 143px;
      animation-delay: 0s;
      border-color: $color-primary;
    }

    &--2 {
      left: 10%;
      width: 28px;
      height: 70px;
      animation-duration: 12s;
      animation-delay: 2s;
      border-color: $color-secondary;
    }

    &--3 {
      left: 70%;
      width: 18px;
      height: 48px;
      animation-delay: 4s;
      border-color: #80e9a0;
    }

    &--4 {
      left: 40%;
      width: 56px;
      height: 143px;
      border: 3px solid green;
      animation-delay: 0s;
      animation-duration: 18s;
      border: 5px solid #dd82fd;
    }

    &--5 {
      left: 60%;
      width: 16px;
      height: 40px;
      animation-delay: 0s;
      border: 3px solid $color-primary;
    }

    &--6 {
      left: 75%;
      width: 28px;
      height: 70px;
      animation-delay: 3s;
      border-color: $color-secondary;
    }

    &--7 {
      left: 35%;
      width: 14px;
      height: 36px;
      animation-delay: 7s;
      border: 3px solid #dd82fd;
    }

    &--8 {
      left: 50%;
      width: 28px;
      height: 70px;
      animation-delay: 15s;
      animation-duration: 45s;
      border-color: #80e9a0;
    }

    &--9 {
      left: 16%;
      width: 40px;
      height: 102px;
      animation-delay: 2s;
      animation-duration: 35s;
      border-color: $color-secondary;
    }

    &--10 {
      left: 85%;
      width: 44px;
      height: 114px;
      animation-delay: 0s;
      animation-duration: 11s;
      border-color: #dd82fd;
    }

    &--11 {
      left: 89%;
      width: 14px;
      height: 36px;
      animation-delay: 1s;
      animation-duration: 13s;
      border: 3px solid $color-primary;
    }

    &--12 {
      left: 94%;
      width: 44px;
      height: 114px;
      animation-delay: 20s;
      animation-duration: 14s;
      border: 5px solid #80e9a0;
    }

    &--13 {
      left: 56%;
      width: 28px;
      height: 70px;
      animation-delay: 15s;
      animation-duration: 12s;
    }

    &--14 {
      left: 47%;
      width: 40px;
      height: 102px;
      animation-delay: 5s;
      animation-duration: 10s;
      border-color: $color-secondary;
    }
  }
}

@keyframes animate-shapes {
  0% {
    transform: translateY(0) rotate(0deg) scale(0.5);
    opacity: 1;
    // border-radius: 0;
  }

  100% {
    transform: translateY(-1000px) rotate(720deg) scale(1.6);
    opacity: 0;
    // border-radius: 50%;
  }
}
